@import '../../../variables.scss';

.final-page {
  &__head {
    font-weight: normal;
    font-size: 24px;
    color: $mainBlack;
  }
  &__nav {
    border-top: 1px solid $grayLight;
    border-bottom: 1px solid $grayLight;
    padding: 2px 64px;
    p {
      color: $mainBlack;
      margin: 0px;
      font-weight: bold;
      font-size: 14px;
    }
  }

  &__container {
    box-sizing: border-box;
    height: 90%;
    display: flex;
    position: relative;
    &__info {
      height: 90%;
      flex: 4;
      padding: 32px 0px 0px 64px;
    }
    &__total {
      flex: 2;
    }
  }
}
@media (max-width: $desktopMini) {
  .final-page {
    &__nav {
      padding: 2px 32px;
    }
    &__container {
      &__info {
        padding: 16px 0px 0px 64px;
      }
      &__total {
        flex: 2;
      }
    }
  }
}

@media (max-width: $tablet) {
  .final-page {
    &__nav {
      padding: 2px 12px;
    }
    &__container {
      &__info {
        padding: 16px;
      }
      &__total {
        flex: 2;
      }
    }
  }
}
@media (max-width: $smart) {
  .final-page {
    margin: 0px;
    overflow-y: auto;
    &__nav {
      padding: 2px 8px;
    }
    &__container {
      flex-direction: column;

      &__info {
        padding: 8px;
      }
    }
  }
}
